import React, { useEffect } from "react";
import cover from "../../../img/cover/cover-dev.webp";
import urlStructure from "../../../img/dev/url-structure.png";
// @ts-ignore
import Prism from "prismjs";
import "prismjs/themes/prism-tomorrow.css";
import "prismjs/plugins/line-numbers/prism-line-numbers.js";
import "prismjs/plugins/line-numbers/prism-line-numbers.css";

const URL = () => {
  useEffect(() => Prism.highlightAll(), []);

  return (
    <>
      <div className="cover-wrapper">
        <img src={cover} alt="dev cover" className="cover" />
      </div>
      <article className="article article-content">
        <h1>Podstawowa struktura adresw URL</h1>

        <p>Oto przykad adresu URL:</p>
        <div className="article-img-wrapper">
          <img src={urlStructure} alt="url structure" className="article-img article-img-desktop" />
        </div>

        <p>Adres URL ma okrelon struktur opart na nastpujcym szablonie:</p>
        <pre className="line-numbers language-html">
          <code>
{`<protocol>://<login>:<password>@<host>:<port>/<path>?<request parameters>#<anchor>`}
          </code>
        </pre>

        <p>Przyjrzyjmy si teraz bardziej szczegowo temu szablonowi:</p>
        <ul className="terminal-list">
          <li>
            <span>&lt;protocol&gt;</span>{" "}
            to sposb wymiany danych z zasobem, prawdopodobnie najlepiej znasz protokoy HTTP i
            HTTPS, ale s te inne
          </li>

          <li>
            <span>&lt;login&gt;</span> i <span>&lt;password&gt;</span>{" "}
            s prefiksami, ktre w razie potrzeby przesyaj dane uwierzytelniajce dla niektrych
            protokow
          </li>

          <li>
            <span>&lt;host&gt;</span>{" "}
            to nazwa domeny lub adres IP, pod ktrym znajduje si witryna (domena to nazwa witryny,
            jej adres w sieci globalnej)
          </li>

          <li>
            <span>&lt;port&gt;</span>{" "}
            jest wymagany do poczenia w ramach okrelonego hosta. Oficjalny port dla pocze HTTP
            to 80, a alternatywny to 8080, ale moliwe jest rwnie uycie innych portw. Domylne
            ustawienie protokou HTTPS to 443
          </li>

          <li>
            <span>&lt;path&gt;</span> wskazuje dokadny adres konkretnego pliku lub strony
          </li>

          <li>
            <span>&lt;request parameters&gt;</span>{" "}
            to parametry przesyane do serwera. W zalenoci od parametrw dania witryna moe
            nieznacznie zmieni sposb wywietlania. Na przykad moliwe jest sortowanie pozycji
            listy w innej kolejnoci
          </li>

          <li>
            <span>&lt;anchor&gt;</span>{" "}
            umoliwia poczenie si z okrelon czci strony internetowej lub dokumentu
          </li>
        </ul>
      </article>
    </>
  );
};

export default URL;
