import React, { useEffect } from "react";
import cover from "../../../img/cover/cover-java.webp";
// @ts-ignore
import Prism from "prismjs";
import "prismjs/themes/prism-tomorrow.css";
import "prismjs/plugins/line-numbers/prism-line-numbers.js";
import "prismjs/plugins/line-numbers/prism-line-numbers.css";

const Stream = () => {
  useEffect(() => Prism.highlightAll(), []);

  return (
    <>
      <div className="cover-wrapper">
        <img src={cover} alt="java cover" className="cover" />
      </div>
      <article className="article article-content">
        <h1>Stream</h1>
        <p>
          Stream w najwikszym skrcie oznacza sekwencj elementw. Stosuje si po to aby zapewni
          wicej czytelnoci oraz atwiej i szybciej mc napisa kod wykonujcy bardziej
          skomplikowane operacje na duych zbiorach danych. Kod co prawda jest krtszy, ale nie jest
          szybszy ni np. ptla, poniewa do kadego elementu kolekcji, na ktrym operujemy{" "}
          <strong>tworzony</strong> jest obiekt, ktry musi
          <strong>przetworzy</strong> dane, a na koniec GarbageCollector musi go{" "}
          <strong>pochon</strong>. Tworz si klasy anonimowe, po zakoczeniu streama nie maj
          ju adnej referencji, wic mona je usun.
        </p>

        <p>Na przykad taki zapis:</p>
        <pre className="line-numbers language-java">
          <code>
{`public static void printAllArcadeNames(List<Arcade> arcades) {
  arcades.stream()
    .map(Arcade::getName)
    .forEach(System.out::println);
}`}
          </code>
        </pre>

        <p>jest tym samym co:</p>
        <pre className="line-numbers language-java">
          <code>
{`public static void printAllArcadeNames(List<Arcade> arcades) {
  arcades.stream()
    .map(new Function<Arcade, String>() {
      @Override
      public String apply(Arcade arcade) {
        return arcade.getName();
      }
    })
    .forEach(new Consumer<String>() {
      @Override
      public void accept(String s) {
          System.out.println(s);
      }
    });
}`}
          </code>
        </pre>

        <hr />

        <h1>Metody</h1>
        <ul className="snippet-list">
          <li>
            <span>.map(Function&lt;T, R&gt;)</span>{" "}
            - uywamy po to, by "mapowa" obiekty, czyli przeksztaca jeden obiekt w inny.
          </li>
          <pre className="line-numbers language-java">
            <code>
{`List<String> letters = Arrays.asList("a", "b", "c", "d");
List<String> collect = letters.stream()
    .map(String::toUpperCase)
    .collect(Collectors.toList());

System.out.println(collect); //[A, B, C, D]`}
            </code>
          </pre>

          <p>
            W tym przykadzie stworzylimy now list, w ktrej Stringi zostay zmapowane na Stringi
            pisane wielk liter.
          </p>

          <li>
            <span>.filter(Predicate&lt;T&gt;)</span>{" "}
            - bdziemy uywa do "filtrowania" naszego streama, czyli wybierania tylko podanych
            elementw.
          </li>
          <pre className="line-numbers language-java">
            <code>
{`List<Person> persons = ...
Stream<Person> personsOver18 = persons.stream()
  .filter(p -> p.getAge() > 18);`}
            </code>
          </pre>

          <p>W tym przykadzie otrzymalimy stream z osobami powyej 18 lat.</p>

          <li>
            <span>.flatMap(Function&lt;T, R&gt;)</span>{" "}
            - ta metoda uywana jest do "spaszczania" streama streamw.
          </li>
          <pre className="line-numbers language-java">
            <code>
{`List<String> pinkFloyd = Arrays.asList("Gilmour", "Waters", "Wright", "Mason", "Barrett");
List<String> ledZeppelin = Arrays.asList("Page", "Plant", "Jones", "Bonham");
List<List<String>> woodstockBands = new ArrayList<>();
  woodstockBands.add(pinkFloyd);
  woodstockBands.add(ledZeppelin);

List<String> lineUp = woodstockBands.stream()
  .flatMap(x -> x.stream())
  .collect(Collectors.toList());`}
            </code>
          </pre>

          <p>
            W tym przykadzie "spaszczylimy" list list na jedn list. Wynikiem jest lista
            Stringw zawierajca wszystkie Stringi z listy rdowej.
          </p>

          <li>
            <span>collect(Collector&lt;T, A, R&gt;)</span>{" "}
            - tej metody mona uy do "zebrania" streama w kolekcj, ktra nas interesuje. Stream
            trzeba w jaki sposb zamkn. Najczciej bdziemy oczekiwa kolekcji, ale mog to by
            pojedyncze obiekty. Poniej przykady:
          </li>
          <pre className="line-numbers language-java">
            <code>
{`List<String> result = givenList.stream()
  .collect(toList());

Set<String> result = givenList.stream()
  .collect(toSet());

List<String> result = givenList.stream()
  .collect(toCollection(LinkedList::new))

String result = givenList.stream()
  .collect(joining());

String result = givenList.stream()
  .collect(joining(" "));

Long result = givenList.stream()
  .collect(counting());`}
            </code>
          </pre>
        </ul>

        <hr />

        <h1>Czym s lambdy?</h1>
        <p>
          Java to obiektowy jzyk programowania, jednak lambdy pozwalaj na pisanie kodu w sposb
          funkcyjny. Oznacza to, e zamiast operowa na stanach obiektw moemy bezporednio
          deklarowa, co chcemy zrobi. Odnoszc lambdy do programowania obiektowego, moemy o nich
          myle jak o klasach tymczasowych zawierajcych jedn metod. Lambdy to obiekty,
          zawierajce fragment kodu: funkcj, a take specyficzne atrybuty i parametry wane dla
          nich (rodowisko, w ramach ktrego operuje funkcja). Wyraenia lambda wykorzystuj
          koncept, ktry nazywa si{" "}
          <i>deferred execution</i>, czyli takie, ktre s wykonywanie z odroczeniem.
        </p>

        <p>Przykad</p>
        <pre className="line-numbers language-java">
          <code>
{`c -> c.canSing();`}
          </code>
        </pre>
        <p className="snippets">
          Taki zapis mwi Javie, e ma wywoa metod z Celebrity jako parametrem i zwrci boolean
          jako wynik <span>c.canSing()</span>. Rwnowany zapis poniej:
        </p>
        <pre className="line-numbers language-java">
          <code>
{`(Celebrity c) -> { return c.canSing(); }`}
          </code>
        </pre>

        <p>
          To co jeszcze warto wiedzie o lambdach, to to, e maj one dostp do zmiennych. Warto
          pamita, e wyraenia lambda maj dostp do pl klasy oraz statycznych zmiennych. Jeli
          chodzi o parametry metody i zmienne lokalne, to ten dostp jest moliwy tylko wtedy, gdy
          nie przypisujemy do nich nowych wartoci. Czyli:
        </p>
        <pre className="line-numbers language-java">
          <code>
{`(a, b) -> { int a = 0; return 5;}  // kod si nie skompiluje

(a, b) -> { int c = 0; return 5; } // to jest ok`}
          </code>
        </pre>
      </article>
    </>
  );
};

export default Stream;
