import React, { useEffect } from "react";
import cover from "../../../img/cover/cover-java.webp";
// @ts-ignore
import Prism from "prismjs";
import "prismjs/themes/prism-tomorrow.css";
import "prismjs/plugins/line-numbers/prism-line-numbers.js";
import "prismjs/plugins/line-numbers/prism-line-numbers.css";
import "prismjs/components/prism-java";

const Optional = () => {
  useEffect(() => Prism.highlightAll(), []);

  return (
    <>
      <div className="cover-wrapper">
        <img src={cover} alt="java cover" className="cover" />
      </div>
      <article className="article article-content">
        <h1>Optional</h1>
        <p className="snippets">
          To generyczny pojemnik a zmienn dowolnego typu, ktra moe mie warto{" "}
          <span>null</span>. Poprzez szereg metod dostpnych w klasie powinnimy by w stanie
          zabezpieczy si przed przypadkowym odwoaniem do <span>null</span> referencji. Obiekty
          {" "}
          <span>Optional</span> tworzy si za pomoc jeden z metod:
        </p>

        <ul className="snippet-list">
          <li>
            <span>empty()</span> - tworzy pusty <span>Optional</span> z wartoci <span>null</span>
            {" "}
            w rodku
          </li>

          <li>
            <span>of(T value)</span> - tworzy <span>Optional</span>{" "}
            z podan wartoci. W przypadku przekazania <span>null</span> dostaniemy{" "}
            <span>nullPointerException</span>
          </li>

          <li>
            <span>ofNullable(T value)</span> - rwnie tworzy <span>Optional</span>{" "}
            z podan wartoci, ale w przypadku przekazania null nie zostanie zgoszony wyjtek
          </li>

          <li>
            <span>Optional(T value)</span>{" "}
            - konstruktor rzucajcy bd w przypadku przekazania wartoci
            <span>null</span>
          </li>
        </ul>

        <p className="snippets">
          Dobrze, wiemy ju jak utworzy <span>Optional</span>{" "}
          to teraz pytanie jak go uy? Dwie najczciej uywane metody to:
        </p>

        <ul className="snippet-list">
          <li>
            <span>isPresent()</span> - zwraca <span>boolean</span>{" "}
            mwicy czy w rodku znajduje si jaka warto czy te <span>null</span>
          </li>

          <li>
            <span>get()</span> - pobranie przechowywanego obiektu. Jeeli takiego nie dostaniemy:
            <span>NoSuchElementException</span>
          </li>
        </ul>

        <p className="snippets">
          Przykad poprawnego wykorzystania{" "}
          <span>Optional</span>: Zamiast budowania zabezpiecze przed
          <span>null</span> jak poniej:
        </p>
        <pre className="line-numbers language-js">
            <code>
{`private String getCompanyFirstUserName1(final Holding holding) {
  if (holding != null) {
    final Company company = holding.getCompanies().get(0);

    if (company != null && company.getUsers() != null) {
      final User user = company.getUsers().get(0);

      if (user != null && user.getFirstName() != null) {
        final String result = user.getFirstName();

        if (result.length() > 0) {
          return result;
        }
      }
    }
  }

  return "not found";
}`}
          </code>
        </pre>

        <p className="snippets">
          mona zapisa za pomoc <span>Optional</span>w takiej formie:
        </p>
        <pre className="line-numbers language-js">
          <code>
{`private String getCompanyFirstUserName2(final Holding holding) {
  return Optional.ofNullable(holding)
    .map(Holding::getCompanies)
    .map(Vector::firstElement)
    .map(Company::getUsers)
    .map(Vector::firstElement)
    .map(User::getFirstName)
    .filter(name -> name.length() > 0)
    .orElse("not found");
}`}
          </code>
        </pre>
      </article>
    </>
  );
};

export default Optional;
